.tooltip {
  position: absolute;
  text-align: left;
  padding: 10px;
  font: 18px sans-serif;
  background: white;
  border: 1px solid black;
  border-radius: 5px;
  pointer-events: none; /* Makes sure the tooltip doesn't interfere with other mouse events */
}

